// Page styles
.page {
  width: 100vw;
  height: 100vh;

  &:not(:first-child) {
    @include margin(nul null null -4px);
  }

  &--half {
    position: relative;
    width: 50vw;
    z-index: -1;

    img {
      transform: scale(1);
    }
  }
}

// Landing section
.landing {
  // background: url(#{$themeimages}/landing-img.jpg) $black center center fixed no-repeat;
  position: relative;

  img {
    object-fit: cover;
    object-position: center;
    height: 100%;
    width: 100%;
  }

  &__title {
    bottom: 80px;
    left: 80px;
    max-width: 490px;
    position: absolute;
  }
}

// Business section
.business {
  background: $butter;

  * {
    color: $black;
  }

  &__title {
    bottom: 80px;
    left: 80px;
    position: absolute;

    @include grid-media($md) {
      bottom: 0;
      height: 100%;
      right: 0;
      width: 100%;
      z-index: 0;
    }

    h1 {
      @include font(120px, 98px);
      color: $white;

      @include grid-media($md) {
        @include font(60ch, 98px);
        position: absolute;
        bottom: 30%;
      }
    }
    
    &-1 {
      clip-path: polygon(0 38%, 114.5% 38%, 114.5% 90%,0 90%);
      z-index: 1;

      h1 {
        color: $black;
      }
    }
  }

  &__subtitle {
    @include grid-media($md) {
      @include margin(13vh auto null);
      max-width: 645px;
    }
  }

  &__image,
  &__image-1,
  &__image-2 {
    bottom: 0;
    position: absolute;
  }

  &__image {
    right: 25%;
  }

  &__image-1 {
    bottom: 15%;
    left: 4%;
    max-width: 40vw;
  }

  &__image-2 {
    bottom: 25%;
    left: auto;
    right: -5%;
  }
}

// Stories section
.stories {
  background: $dark-green;
  position: relative;
  z-index: -2;

  @include grid-media($md) {
    @include padding(8% null null);
  }

  * {
    color: $white;
  }

  &__title {
    @include grid-media($md) {
      @include margin(null null 1.25%);
    }
  }

  &__img {
    @include grid-media($md) {
      @include margin(2.5% null);
      height: 50vh;
    }
  }

  &__wrapper {
    @include margin(auto null);
    align-items: center;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    position: relative;
    z-index: 1;
  }

  &__subnav {
    display: flex;

    @include grid-media($md) {
      width: 26%;
    }

    li {
      &:not(:last-child) {
        @include margin(null 4% null null);
      }

      a {
        @include font(16px, 19px);
      }
    }
  }

  .large-title {
    bottom: 30%;
    position: absolute;
    left: 0;

    h4 {
      @include font(100px, 168px);

      @include grid-media($md) {
        @include font(160px, 168px);
      }
    }
  }
}

// Scroll Override
.scroll-container {
  left: 0;
  overflow: hidden;
  
  @include grid-media($md) {
    display: flex;
    height: 100vh;
  }
}

// Hide the custom scrollbar
.c-scrollbar {
  display: none;
}
