/**
  TYPOGRAPHIC
  global typocraphic constants
**/
h1,
h2,
h3,
h4 {
  white-space: normal; }

h1,
h2,
h3 {
  color: #FFFFFF;
  font-family: "Poppins";
  font-weight: 400; }

h4 {
  color: #FFFFFF;
  font-family: "Cormorant";
  font-weight: 400; }

h1 {
  font-size: 50px;
  line-height: 58px; }
  @media only screen and (min-width: 1024px) {
    h1 {
      font-size: 60px;
      line-height: 68px; } }

h2 {
  font-size: 30px;
  line-height: 48px; }
  @media only screen and (min-width: 1024px) {
    h2 {
      font-size: 40px;
      line-height: 48px; } }

h3 {
  font-size: 28px;
  line-height: 38px; }
  @media only screen and (min-width: 1024px) {
    h3 {
      font-size: 30px;
      line-height: 38px; } }

h4 {
  font-size: 20px;
  line-height: 38px; }

a {
  font-size: 16px;
  line-height: 150%;
  font-family: "Poppins";
  transition: 0.3s; }
  @media only screen and (min-width: 768px) {
    a {
      font-size: 20px;
      line-height: 170%; } }

